@font-face {
  font-family: "karla";
  src: url("../../fonts/Karla-Regular.ttf");
  font-weight: 400;
  font-style: regular;
}
@font-face {
  font-family: "karla";
  src: url("../../fonts/Karla-Bold.ttf");
  font-weight: 700;
  font-style: bold;
}
@font-face {
  font-family: "DM Serif";
  src: url("../../fonts/DMSerifDisplay-Regular.ttf");
  font-weight: 400;
  font-style: regular;
}
:root {
  /*=======PRIMARY=======*/
  --clr-primary-100: #95a9c6;
  --clr-primary-110: #2d2640;
  /*=======NEUTRAL=======*/
  --clr-neutral-100: #fafafa;
  --clr-neutral-110: #837d87;
  --clr-neutral-120: #2b272f;
  /*=======FAMILY=======*/
  --ff-family-karla: 'Karla', sans-serif;
  --ff-family-dm: 'DM Serif Display', serif;
  /*=======WEIGHT=======*/
  --fw-weight-regular: 400;
  --fw-weight-bold: 700;
  /*=======SIZE=======*/
  --fs-size-3xs: 0.8125rem;
  --fs-size-2xs: 0.875rem;
  --fs-size-xs: 1rem;
  --fs-size-sm: 1.375rem;
  --fs-size-md: 1.6875rem;
  --fs-size-lg: 2.4375rem;
  --fs-size-xl: 2.9375rem;
  --fs-size-2xl: 3.4375rem;
  --fs-size-3xl: 4.375rem;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p {
  margin: 0;
}

ul {
  list-style: none;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  text-align: center;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
  height: auto;
}

/* Inherit fonts for inputs and buttons */

button {
  font: inherit;
}

/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
*:focus-visible {
  outline: 2px dashed red;
}

.wrapper {
  max-width: 1900px;
  outline: 1px solid #929292;
  margin: auto;
}

a {
  text-decoration: none;
}

.attribution {
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: #3e52a3;
}

/*---Menu Button---*/
.menu-btn {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: 0.5s;
  background: transparent;
  border: 2px solid var(--clr-neutral-120);
  width: 2rem;
  height: 2rem;
}
.menu-btn__burger {
  position: absolute;
  width: 1.25rem;
  height: 0.15625rem;
  background: var(--clr-neutral-120);
  border-radius: 5px;
  transition: 0.5s;
}
.menu-btn__burger::before, .menu-btn__burger::after {
  content: "";
  position: absolute;
  width: inherit;
  height: inherit;
  background: var(--clr-neutral-120);
  border-radius: 5px;
  transition: 0.5s;
  left: 0;
}
.menu-btn__burger::before {
  transform: translateY(-0.375rem);
}
.menu-btn__burger::after {
  transform: translateY(0.375rem);
}
.menu-btn:hover .menu-btn__burger::after {
  transform: translate(3px, 6px);
}
.menu-btn:hover .menu-btn__burger::before {
  transform: translate(3px, -6px);
}
.menu-btn .menu-btn__burger:not(.active) {
  background: transparent;
}
.menu-btn .menu-btn__burger:not(.active)::before {
  transform: rotate(45deg);
}
.menu-btn .menu-btn__burger:not(.active)::after {
  transform: rotate(-45deg);
}

.outline-btn {
  display: block;
  height: 2.5rem;
  width: 9rem;
  border: 2px solid var(--clr-neutral-100);
  color: var(--clr-neutral-100);
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  font-size: var(--fs-size-xs);
  font-weight: var(--fw-weight-bold);
  font-family: var(--ff-family-karla);
  letter-spacing: 0.03125rem;
}

